body {
  margin: 0;
  padding: 0;
  background-color: #2c2c2c; /* Dark grey background */
  font-family: Arial, sans-serif;
  color: #ffffff; /* Light text color */
}

.login-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-form {
  background-color: rgba(255, 255, 255, 0.05); /* Increase transparency */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Subtle border */
  padding: 2rem;
  border-radius: 12px; /* Rounded corners */
  width: 350px; /* Slightly wider form */
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.6); /* Stronger shadow for depth */
  backdrop-filter: blur(15px); /* Stronger blur for glass effect */
  -webkit-backdrop-filter: blur(15px); /* Glass effect for Safari */
}

.login-form h2 {
  margin-bottom: 1.5rem;
  text-align: center;
  color: #61dafb; /* Accent color for the heading */
}

.login-form input {
  width: 95%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #444; /* Darker border */
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.1); /* Semi-transparent input background */
  color: #ffffff; /* Light text color */
}

.login-form input::placeholder {
  color: #bbb; /* Subtle placeholder color */
}

.login-form button {
  width: 100%;
  padding: 0.75rem;
  background-color: #61dafb; /* Accent color for the button */
  color: #121212; /* Dark text color for contrast */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

.login-form button:hover {
  background-color: #4db8e6; /* Slightly darker hover effect */
}

.login-form .forgot-password {
  margin-top: 1rem;
  text-align: center;
}

.login-form .forgot-password a {
  color: #61dafb; /* Accent color for the link */
  text-decoration: none;
}

.login-form .forgot-password a:hover {
  text-decoration: underline;
}

.register-link {
  margin-top: 10px;
  font-size: 0.8rem; /* Small text size */
  color: #61dafb; /* Accent color for the link */
  cursor: pointer; /* Pointer cursor to indicate it's clickable */
  text-align: left; /* Align text to the left */
  display: block; /* Ensure it takes up its own line */
}

.register-link:hover {
  text-decoration: underline; /* Underline on hover for better visibility */
}

.demo-accounts {
  margin-top: 20px;
  padding: 10px 15px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.demo-title {
  font-weight: bold;
  margin-bottom: 8px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.9);
  text-align: center;
}

.account-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.account-list p {
  margin: 0;
  font-size: 12px;
}

.account-list strong {
  margin-right: 5px;
  color: #61dafb;
}