/* Banner notification styles */
.legal-document-banner {
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  max-width: 600px;
  background-color: #2c2c2c;
  color: #ffffffc6;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.legal-document-banner-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  background-color: #363636;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.legal-document-banner-title {
  display: flex;
  align-items: center;
  font-weight: 600;
}

.banner-icon {
  color: #61dafb;
  font-size: 20px;
  margin-right: 10px;
}

.legal-document-banner-controls {
  display: flex;
  gap: 8px;
}

.legal-document-banner-content {
  padding: 16px;
  max-height: 70vh;
  overflow-y: auto;
}

.legal-document-description {
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 16px;
}

.legal-document-upload {
  width: 100%;
  margin-bottom: 16px;
}

.legal-document-upload .ant-upload-list {
  max-height: 150px;
  overflow-y: auto;
  margin-top: 15px;
}

.legal-document-upload .ant-btn {
  background-color: rgba(97, 218, 251, 0.1);
  border: 1px solid #61dafb;
  color: #61dafb;
  transition: all 0.3s;
}

.legal-document-upload .ant-btn:hover {
  background-color: rgba(97, 218, 251, 0.2);
  border: 1px solid #61dafb;
  color: #ffffff;
}

.legal-document-requirements {
  background-color: rgba(255, 255, 255, 0.05);
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 12px;
  font-size: 14px;
}

.legal-document-requirements h4 {
  margin-top: 0;
  margin-bottom: 8px;
  color: #ffffff;
  font-size: 14px;
}

.legal-document-requirements ul {
  margin: 0;
  padding-left: 20px;
}

.legal-document-requirements li {
  margin-bottom: 6px;
  color: #ffffffc6;
}

.legal-document-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}

.legal-document-actions .ant-btn-primary {
  background-color: #61dafb;
  border-color: #61dafb;
  color: #000000;
}

.legal-document-actions .ant-btn-primary:hover {
  background-color: #4db8e6;
  border-color: #4db8e6;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .legal-document-banner {
    width: 95%;
    top: 5px;
  }
}

/* Original modal styles for backward compatibility */
.legal-document-modal .ant-modal-content {
  background-color: #2c2c2c;
  color: #ffffffc6;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.legal-document-modal .ant-modal-header {
  background-color: #363636;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding: 16px 24px;
}

.legal-document-modal .ant-modal-title {
  color: #ffffff;
  font-weight: 600;
}

.legal-document-modal .ant-modal-close {
  color: #ffffffc6;
}

.legal-document-modal .ant-modal-close:hover {
  color: #ffffff;
}

.legal-document-modal .ant-modal-body {
  padding: 24px;
}

.legal-document-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.legal-document-icon {
  font-size: 48px;
  color: #61dafb;
  text-align: center;
  margin-bottom: 10px;
}

.legal-document-note {
  margin-top: 15px;
  color: #aaaaaa;
  font-size: 14px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 15px;
} 