/* ProStudentWorkshop.css */

.recorded-workshop-placeholder {
  position: relative;
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
}

.recorded-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
}

.recording-status {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.workshop-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  max-width: 100px;
}

/* Live workshop styles */
.live-dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #ff4d4f;
  border-radius: 50%;
  margin-right: 5px;
  animation: pulse 1.5s infinite;
}

.live-indicator {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-weight: bold;
  display: flex;
  align-items: center;
  z-index: 10;
}

.speaker-info {
  position: absolute;
  bottom: 10px;
  left: 10px;
  z-index: 10;
}

.speaker-bubble {
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 8px 12px;
  border-radius: 4px;
}

.speaker-name {
  font-weight: bold;
}

.speaker-title {
  font-size: 12px;
  opacity: 0.8;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Registration notification styles */
.registration-notification {
  background-color: #f0f5ff;
  border: 1px solid #adc6ff;
  border-radius: 4px;
  padding: 10px;
  margin-top: 10px;
}

.registration-notification-title {
  font-weight: bold;
  color: #1890ff;
  margin-bottom: 5px;
}

.registration-notification-date {
  color: #52525b;
  font-size: 13px;
} 