.submit-evaluations-container {
  min-height: 100vh;
  background-color: #1a1a1a;
  color: #ffffff;
}

.evaluations-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.evaluations-header {
  text-align: center;
  margin-bottom: 40px;
}

.evaluations-header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: #ffffff;
}

.info-text {
  color: #b3b3b3;
  font-size: 1.1rem;
}

.section-container {
  background-color: #2d2d2d;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 30px;
}

.section-container h2 {
  color: #ffffff;
  margin-bottom: 20px;
  font-size: 1.8rem;
}

.internships-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.internship-card {
  background-color: #3d3d3d;
  border-radius: 8px;
  padding: 20px;
  transition: transform 0.2s;
}

.internship-card:hover {
  transform: translateY(-5px);
}

.internship-header {
  margin-bottom: 15px;
}

.internship-header h3 {
  color: #ffffff;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.company-info {
  display: flex;
  flex-direction: column;
  gap: 5px;
  color: #b3b3b3;
  font-size: 0.9rem;
}

.internship-actions {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

.submit-evaluation-button,
.view-evaluation-button,
.create-report-button,
.view-report-button {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.submit-evaluation-button {
  background-color: #0077b5;
  color: white;
}

.view-evaluation-button {
  background-color: #28a745;
  color: white;
}

.create-report-button {
  background-color: #6f42c1;
  color: white;
}

.view-report-button {
  background-color: #fd7e14;
  color: white;
}

.submit-evaluation-button:hover,
.view-evaluation-button:hover,
.create-report-button:hover,
.view-report-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.submit-evaluation-button:hover {
  background-color: #005582;
}

.view-evaluation-button:hover {
  background-color: #218838;
}

.create-report-button:hover {
  background-color: #5a32a3;
}

.view-report-button:hover {
  background-color: #e56b0c;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.evaluation-modal {
  background-color: #2d2d2d;
  border-radius: 10px;
  padding: 30px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  margin-bottom: 20px;
}

.modal-header h2 {
  color: #ffffff;
  margin-bottom: 10px;
}

.internship-info {
  color: #b3b3b3;
}

.evaluation-details {
  margin: 20px 0;
}

.detail-group {
  margin-bottom: 20px;
}

.detail-group h4 {
  color: #ffffff;
  margin-bottom: 10px;
}

.detail-group p {
  color: #b3b3b3;
  line-height: 1.5;
}

.evaluation-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: #ffffff;
  font-weight: 500;
}

.form-group textarea {
  padding: 12px;
  border-radius: 5px;
  border: 1px solid #4d4d4d;
  background-color: #3d3d3d;
  color: #ffffff;
  min-height: 100px;
  resize: vertical;
}

.form-group textarea:focus {
  outline: none;
  border-color: #0077b5;
}

.rating {
  display: flex;
  gap: 5px;
  align-items: center;
}

.star {
  font-size: 28px;
  color: #4d4d4d;
  transition: all 0.2s ease;
  user-select: none;
}

.star.filled {
  color: #ffd700;
  text-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
}

.star:hover {
  transform: scale(1.1);
}

.star[style*="cursor: pointer"]:hover {
  color: #ffd700;
}

.star[style*="cursor: pointer"]:hover ~ .star {
  color: #4d4d4d;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
}

.edit-button,
.delete-button,
.close-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
  font-weight: 500;
}

.edit-button {
  background-color: #28a745;
  color: white;
}

.delete-button {
  background-color: #dc3545;
  color: white;
}

.close-button {
  background-color: #6c757d;
  color: white;
}

.edit-button:hover {
  background-color: #218838;
}

.delete-button:hover {
  background-color: #c82333;
}

.close-button:hover {
  background-color: #5a6268;
}

.no-data-message {
  text-align: center;
  color: #b3b3b3;
  padding: 40px;
}

/* Report Section Styles */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.create-report-button {
  background-color: #0077b5;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.create-report-button:hover {
  background-color: #005582;
  transform: translateY(-2px);
}

.reports-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.report-card {
  background-color: #3d3d3d;
  border-radius: 8px;
  padding: 20px;
  transition: all 0.3s ease;
}

.report-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.report-header {
  margin-bottom: 15px;
}

.report-header h3 {
  color: #ffffff;
  margin-bottom: 8px;
  font-size: 1.2rem;
}

.report-date {
  color: #b3b3b3;
  font-size: 0.9rem;
}

.report-preview {
  color: #b3b3b3;
  margin-bottom: 15px;
  line-height: 1.5;
}

.report-actions {
  display: flex;
  gap: 10px;
}

.view-button {
  flex: 1;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.view-button:hover {
  background-color: #218838;
  transform: translateY(-2px);
}

/* Form Input Styles */
.form-group input {
  padding: 12px;
  border-radius: 5px;
  border: 1px solid #4d4d4d;
  background-color: #3d3d3d;
  color: #ffffff;
  width: 100%;
}

.form-group input:focus {
  outline: none;
  border-color: #0077b5;
}

/* Responsive Design */
@media (max-width: 768px) {
  .evaluations-header h1 {
    font-size: 2rem;
  }

  .internships-grid {
    grid-template-columns: 1fr;
  }

  .evaluation-modal {
    width: 95%;
    padding: 20px;
  }

  .reports-grid {
    grid-template-columns: 1fr;
  }

  .section-header {
    flex-direction: column;
    gap: 15px;
  }

  .create-report-button {
    width: 100%;
  }

  .internship-actions {
    flex-direction: column;
  }

  .submit-evaluation-button,
  .view-evaluation-button,
  .create-report-button,
  .view-report-button {
    width: 100%;
  }
} 