.register-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #2c2c2c;
  }
  
  .register-form {
    background-color: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    width: 100%;
    max-width: 400px;
    color: #ffffff;
  }
  
  .register-form h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .register-form input {
    width: 95%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #ffffff;
  }
  
  .register-form input::placeholder {
    color: #bbb;
  }
  
  .register-form button {
    width: 100%;
    padding: 10px;
    background-color: #61dafb;
    border: none;
    border-radius: 4px;
    color: #121212;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .register-form button:hover {
    background-color: #4db8e6;
  }
  
  /* Toggle Buttons */
/* Toggle Buttons */
.toggle-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .toggle-button {
    background-color: rgba(255, 255, 255, 0.1); /* Default background */
    border: 1px solid rgba(255, 255, 255, 0.2); /* Subtle border */
    color: #ffffff; /* Default text color */
    padding: 10px 20px;
    margin: 0 5px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .toggle-button.active {
    background-color: rgba(50, 50, 50, 0.8); /* Darker background for active button */
    color: #ffffff; /* Keep text color white for contrast */
    border-color: rgba(50, 50, 50, 0.8); /* Match border color with background */
  }
  
  .toggle-button:hover {
    background-color: rgba(70, 70, 70, 0.8); /* Slightly lighter hover effect */
    color: #ffffff;
  }
  
  .toggle-button:hover {
    background-color: #4db8e6; /* Slightly darker hover effect */
    color: #121212;
  }
  .toggle-container label {
    font-size: 0.9rem;
    color: #61dafb;
    cursor: pointer;
  }
  
  .login-link {
    margin-top: 10px;
    font-size: 0.8rem;
    color: #61dafb;
    text-align: center;
    cursor: pointer;
  }
  
  .login-link:hover {
    text-decoration: underline;
  }

  .file-input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15px;
}

.file-input-label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  border: 2px dashed rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
  background-color: rgba(255, 255, 255, 0.1);
  transition: border-color 0.3s ease, background-color 0.3s ease;
}

.file-input-label:hover {
  border-color: #61dafb;
  background-color: rgba(255, 255, 255, 0.2);
}

.logo-preview {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 8px;
}

.file-input {
  display: none; /* Hide the actual file input */
}

.placeholder-text {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}