.tabs-container {
  margin-top: 20px;
}

.tab-buttons {
  display: flex;
  gap: 12px;
  margin-bottom: 15px;
  border-bottom: 1px solid #333;
}

.tab-button {
  background: none;
  border: none;
  color: #ccc;
  font-weight: bold;
  padding: 10px 15px;
  cursor: pointer;
  border-bottom: 2px solid transparent;
}

.tab-button.active {
  color: #00bcd4;
  border-bottom: 2px solid #00bcd4;
}

.tab-content {
  padding: 20px 0;
}

.no-data {
  color: #888;
  padding: 30px;
  text-align: center;
}
