/* Companies.css */
.companies-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.companies-container h1 {
  color: #f0f0f0;
  margin-bottom: 20px;
  text-align: center;
}

/* Search Bar */
.search-bar {
  margin-bottom: 20px;
}

.search-bar input {
  width: 100%;
  padding: 10px 15px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.05);
  color: #ffffffc6;
  outline: none;
  font-size: 16px;
}

/* Tabs */
.tab-buttons {
  display: flex;
  gap: 5px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.tab-button {
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  padding: 12px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
}

.tab-button:hover {
  color: #ffffffc6;
}

.tab-button.active {
  color: #61dafb;
  font-weight: bold;
}

.tab-button.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #61dafb;
}

/* Tab Content */
.tab-content {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 20px;
  min-height: 400px;
}

.companies-list h2 {
  color: #f0f0f0;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.no-companies {
  text-align: center;
  padding: 40px;
  color: #aaa;
}

/* Company Cards */
.company-card {
  background-color: rgba(255, 255, 255, 0.08);
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 15px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.company-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.company-card.pending {
  border-left: 4px solid #f0ad4e;
}

.company-card.approved {
  border-left: 4px solid #5cb85c;
}

.company-card.rejected {
  border-left: 4px solid #d9534f;
}

.company-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.company-logo img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.company-info {
  flex: 1;
}

.company-info h3 {
  margin: 0 0 5px 0;
  color: #f0f0f0;
  font-size: 1.2rem;
}

.company-industry,
.company-location {
  margin: 0;
  color: #aaa;
  font-size: 0.9rem;
}

.company-status {
  padding: 5px 10px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: bold;
}

.company-status.pending {
  background-color: rgba(240, 173, 78, 0.2);
  color: #f0ad4e;
}

.company-status.approved {
  background-color: rgba(92, 184, 92, 0.2);
  color: #5cb85c;
}

.company-status.rejected {
  background-color: rgba(217, 83, 79, 0.2);
  color: #d9534f;
}

.company-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 15px;
}

.company-actions button {
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.view-details-button {
  background-color: #61dafb;
  color: #121212;
}

.view-details-button:hover {
  background-color: #4dabdc;
}

.approve-button {
  background-color: #5cb85c;
  color: white;
}

.approve-button:hover {
  background-color: #4c9e4c;
}

.reject-button {
  background-color: #d9534f;
  color: white;
}

.reject-button:hover {
  background-color: #c9302c;
}

.company-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.rejection-reason {
  color: #d9534f;
  font-size: 0.9rem;
}

/* Company Details Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.company-details-modal {
  background-color: #2c2c2c;
  border-radius: 8px;
  padding: 20px;
  width: 80%;
  max-width: 800px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.company-details-modal h2 {
  color: #f0f0f0;
  margin-bottom: 20px;
  text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.company-profile-header {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
}

.company-logo-large img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid rgba(255, 255, 255, 0.2);
}

.company-profile-info {
  flex: 1;
}

.company-profile-info h3 {
  margin: 0 0 10px 0;
  color: #f0f0f0;
  font-size: 1.5rem;
}

.company-website a {
  color: #61dafb;
  text-decoration: none;
}

.company-website a:hover {
  text-decoration: underline;
}

.company-section {
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.company-section:last-child {
  border-bottom: none;
}

.company-section h4 {
  color: #61dafb;
  margin-bottom: 10px;
}

.company-section p {
  color: #ffffffc6;
  line-height: 1.5;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
}

.cancel-button {
  background-color: #555;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

.cancel-button:hover {
  background-color: #666;
}

@media (max-width: 768px) {
  .company-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .company-actions {
    flex-direction: column;
    width: 100%;
  }
  
  .company-actions button {
    width: 100%;
  }
  
  .company-footer {
    flex-direction: column;
    gap: 15px;
  }
  
  .company-profile-header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
} 