/* MYNetworks.css */
.mynetworks-container {
    font-family: Arial, sans-serif;
    background-color: #2c2c2c;
    color: #ffffff;
    min-height: 100vh;
  }
  
  .top-panel {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    padding: 10px 20px;
    color: white;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .search-bar {
    display: flex;
    margin-left: 20px;
  }
  
  .search-bar input {
    margin-right: 10px;
    padding: 10px;
    border: 1px solid #444;
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #ffffff;
  }
  
  .search-bar input::placeholder {
    color: #bbb;
  }

  .icon.active {
    color: #61dafb; /* Bright color for the active icon */
    font-weight: bold;
    text-shadow: 0 0 5px rgba(97, 218, 251, 0.8); /* Glow effect */
  }
  
  .icons {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  
  .icon {
    cursor: pointer;
    transition: color 0.3s, text-shadow 0.3s;
  }
  
  .icon:hover {
    color: #4db8e6; /* Slightly brighter on hover */
  }
  
  .icon.profile {
    display: flex;
    align-items: center;
  }
  
  .profile-pic {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 10px;
    border: 2px solid #888888;
    transition: border-color 0.3s ease;
  }
  
  .profile-pic:hover {
    border-color: #000000;
  }
  
  .main-content {
    display: flex;
    gap: 20px;
    padding: 20px;
    margin-top: 20px;
  }
  
  .left-sidebar {
    flex: 0 0 250px;
  }
  
  .right-sidebar {
    flex: 0 0 300px;
  }
  
  .center-content {
    flex: 1;
    max-width: 700px;
  }
  
  /* Profile Card Styles */
  .profile-card {
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .profile-header {
    text-align: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding-bottom: 15px;
    margin-bottom: 15px;
  }
  
  .profile-avatar {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    margin-bottom: 10px;
    border: 2px solid #888888;
  }
  
  .profile-stats {
    display: flex;
    justify-content: space-between;
    margin: 15px 0;
  }
  
  .stat-item {
    text-align: center;
    flex: 1;
  }
  
  .stat-item span {
    display: block;
    font-size: 0.9rem;
    color: #888888;
  }
  
  .stat-item strong {
    font-size: 1.2rem;
  }
  
  .profile-premium {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: rgba(255, 215, 0, 0.1);
    border-radius: 4px;
  }
  
  .pro-badge {
    background-color: #ffd700;
    color: #000;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 0.8rem;
  }
  
  /* Post Box Styles */
  .post-box {
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    padding: 20px;
    margin-top: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  }
  
  .post-textbox {
    width: 100%;
    height: 60px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    padding: 10px;
    font-size: 1rem;
    resize: none;
  }
  
  .post-textbox::placeholder {
    color: #bbb;
  }
  
  .post-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  
  .post-button {
    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: #ffffff;
    padding: 10px 15px;
    font-size: 0.9rem;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .post-button:hover {
    background-color: #61dafb;
    color: #121212;
  }
  
  /* Suggestions Card Styles */
  .suggestions-card {
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    padding: 15px;
  }
  
  .suggestions-card h4 {
    margin-bottom: 15px;
    color: #ffffff;
  }
  
  .suggestion-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.02);
    border-radius: 4px;
  }
  
  .suggestion-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .suggestion-info {
    flex: 1;
  }
  
  .suggestion-info span {
    display: block;
    font-weight: bold;
  }
  
  .suggestion-info p {
    font-size: 0.8rem;
    color: #888888;
    margin: 4px 0;
  }
  
  .connect-button {
    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: #61dafb;
    padding: 4px 10px;
    border-radius: 4px;
    font-size: 0.8rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .connect-button:hover {
    background-color: #61dafb;
    color: #121212;
  }
  
  .suggestion-avatar{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-bottom: 10px;
    border: 2px solid #888888;
  }

  .manage-network-card {
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
  }

  .network-section {
    margin: 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding-bottom: 15px;
  }

  .network-list {
    list-style: none;
    padding: 0;
    margin: 10px 0;
  }

  .network-list li {
    padding: 8px 0;
    cursor: pointer;
    transition: color 0.3s ease;
  }

  .network-list li:hover {
    color: #61dafb;
  }

  .network-footer {
    display: flex;
    gap: 15px;
    margin-top: 20px;
    font-size: 0.9rem;
    color: #888888;
  }

  .suggestions-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .show-all {
    background: none;
    border: none;
    color: #61dafb;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  .games-section {
    margin-bottom: 30px;
  }
  
  .game-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin: 10px 0;
    background-color: rgba(255, 255, 255, 0.02);
    border-radius: 4px;
  }
  
  .game-button {
    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    color: #ffffff;
    padding: 6px 12px;
    border-radius: 4px;
    cursor: pointer;
  }

  .mynetworks-content {
    display: flex;
    flex-direction: column;
    padding: 20px;
    max-width: 1000px;
    margin: 0 auto;
  }

  .flagged-reports-section {
    background-color: #202124;
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  }

  .flagged-reports-section h2 {
    color: white;
    margin-bottom: 8px;
  }

  .section-description {
    color: #aaa;
    margin-bottom: 20px;
  }

  .reports-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  .report-card {
    background-color: #2d2d31;
    border: 1px solid #3d3d41;
    color: white;
  }

  .report-card .ant-card-head {
    background-color: #1f1f23;
    border-bottom: 1px solid #3d3d41;
    color: white;
  }

  .report-meta {
    margin-bottom: 15px;
    color: #ddd;
  }

  .report-comments {
    background-color: #1f1f23;
    padding: 15px;
    border-radius: 4px;
    margin: 10px 0;
    color: #ddd;
  }

  .report-comments h4 {
    color: white;
    margin-bottom: 10px;
  }

  .report-actions {
    margin-top: 15px;
    display: flex;
    justify-content: flex-end;
  }

  .appeal-report-info {
    background-color: #1f1f23;
    padding: 15px;
    border-radius: 4px;
    margin-bottom: 15px;
    color: white;
  }

  .reviewer-reason {
    margin-top: 10px;
    padding: 10px;
    background-color: #2d2d31;
    border-left: 3px solid #1890ff;
    border-radius: 0 4px 4px 0;
  }

  /* Dark mode styling for Ant Design components */
  .ant-modal-content,
  .ant-modal-header {
    background-color: #202124;
    color: white;
    border: none;
  }

  .ant-modal-title {
    color: white;
  }

  .ant-modal-close {
    color: #aaa;
  }

  .ant-modal-close:hover {
    color: white;
  }

  .ant-form-item-label > label {
    color: white;
  }

  .ant-input,
  .ant-input-textarea textarea {
    background-color: #2d2d31;
    color: white;
    border: 1px solid #3d3d41;
  }

  .ant-input:focus,
  .ant-input-textarea textarea:focus {
    border-color: #1890ff;
    box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
  }

  .ant-divider {
    border-top-color: #3d3d41;
  }

  .form-actions {
    display: flex;
    justify-content: flex-end;
  }