.page-header {
  margin-bottom: 20px;
}

.page-header-top {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.page-header-title-container {
  flex: 1;
  margin-left: 15px;
}

.page-header-title {
  font-size: 24px;
  margin: 0;
  color: #fff;
}

.page-header-subtitle {
  font-size: 14px;
  color: #666;
  margin: 4px 0 0 0;
}

.page-header-actions {
  display: flex;
  gap: 10px;
}

/* Dark theme */
.dark-theme .page-header-title {
  color: #fff;
}

.dark-theme .page-header-subtitle {
  color: #bbb;
}

/* Responsive styles */
@media (max-width: 768px) {
  .page-header-top {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .page-header-title-container {
    margin-left: 0;
    margin-top: 10px;
    width: 100%;
  }
  
  .page-header-actions {
    margin-top: 10px;
    width: 100%;
    justify-content: flex-end;
  }
} 