/* AvailableInternships.css */
.available-internships-container {
  font-family: Arial, sans-serif;
  background-color: #2c2c2c;
  color: #ffffffc6;
  min-height: 100vh;
  padding: 20px;
}

/* Header styles */
.internships-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
.company-name {
  font-size: 0.9rem;
  color: #ccc;
  margin-top: 4px;
}

.company-name-large {
  font-size: 1.5rem;
  font-weight: bold;
  color: #61dafb;
  text-align: center;
  margin-top: 10px;
}

.internships-header h1 {
  color: #ffffffc6;
  margin: 0;
  text-align: center !important;
  font-weight: bold;
  font-size: 24px;
}

/* Filter styles */
.internships-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 25px;
  background-color: rgba(255, 255, 255, 0.03);
  padding: 15px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.search-group {
  flex: 1;
  min-width: 150px;
}

.search-group input {
  width: 100%;
  padding: 10px 15px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.05);
  color: #ffffffc6;
  outline: none;
}

.filter-group {
  display: flex;
  flex-direction: column;
  min-width: 180px;
}

.filter-group label {
  margin-bottom: 5px;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: bold;
  color: #ffffffc6 !important;
}

.filter-group select {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.05);
  color: #ffffffc6;
  outline: none;
}

.filter-stats {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  padding: 10px;
}

/* Empty state */
.no-internships {
  text-align: center;
  padding: 40px 20px;
  background-color: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  margin-top: 20px;
}

.no-internships i {
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.3);
  margin-bottom: 15px;
}

.no-internships p {
  font-size: 1.2rem;
  color: #ffffffc6;
  margin-bottom: 10px;
}

.help-text {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

/* Internship Cards */
.internships-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.internship-card {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
}

.internship-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-color: rgba(255, 255, 255, 0.15);
}

.internship-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.internship-info {
  display: flex;
  align-items: center;
  gap: 15px;
}

.company-logo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.company-logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.internship-info h3 {
  margin: 0 0 5px 0;
  font-size: 1.1rem;
  color: #ffffffc6;
}

.company-name {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
}

.internship-status {
  font-size: 0.75rem;
  padding: 5px 10px;
  border-radius: 15px;
  font-weight: bold;
}

.paid-status {
  background-color: rgba(39, 174, 96, 0.15);
  color: #27ae60;
  padding: 5px 10px;
  border-radius: 15px;
}

.unpaid-status {
  background-color: rgba(242, 153, 74, 0.15);
  color: #f2994a;
  padding: 5px 10px;
  border-radius: 15px;
}

.internship-details {
  padding: 15px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.internship-detail {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
}

.detail-label {
  color: rgba(255, 255, 255, 0.7);
}

.detail-value {
  color: #ffffffc6;
  font-weight: 500;
}

.internship-footer {
  padding: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.view-details-button {
  background-color: rgba(97, 218, 251, 0.15);
  color: #61dafb;
  border: 1px solid rgba(97, 218, 251, 0.4);
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.view-details-button:hover {
  background-color: rgba(97, 218, 251, 0.3);
  border-color: #61dafb;
}

.apply-button {
  background-color: rgba(39, 174, 96, 0.15);
  color: #27ae60;
  border: 1px solid rgba(39, 174, 96, 0.4);
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.apply-button:hover {
  background-color: rgba(39, 174, 96, 0.3);
  border-color: #27ae60;
}

/* Modal styles */
.internship-details-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1100;
  padding: 20px;
}

.modal-content {
  background-color: #2c2c2c;
  border-radius: 8px;
  width: 100%;
  max-width: 700px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
}

.modal-header {
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: #2c2c2c;
  z-index: 1;
}

.modal-header h2 {
  margin: 0;
  color: #ffffffc6;
  font-size: 1.5rem;
}

.close-button {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.5rem;
  cursor: pointer;
  transition: color 0.2s;
}

.close-button:hover {
  color: #ffffffc6;
}

.modal-body {
  padding: 20px;
  overflow-y: auto;
}

.company-detail {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.company-logo-large {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 3px solid rgba(255, 255, 255, 0.2);
  object-fit: cover;
}

.company-detail h3 {
  margin: 0;
  color: #ffffffc6;
  font-size: 1.3rem;
}

.detail-section {
  margin-bottom: 25px;
}

.detail-section h4 {
  margin: 0 0 15px 0;
  color: #61dafb;
  font-size: 1.1rem;
  position: relative;
  padding-bottom: 8px;
}

.detail-section h4::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 2px;
  background-color: #61dafb;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 0.95rem;
}

.detail-item .detail-label {
  font-weight: bold;
  color: rgba(255, 255, 255, 0.7);
}

.detail-item .detail-value {
  color: #ffffffc6;
  text-align: right;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.skill-tag {
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffffc6;
  padding: 5px 12px;
  border-radius: 15px;
  font-size: 0.8rem;
  display: inline-block;
}

.description {
  white-space: pre-line;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.8);
}

.modal-footer {
  padding: 15px 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.close-button-large {
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffffc6;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.2s;
}

.close-button-large:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* Application Modal Styles */
.application-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1200;
  padding: 20px;
}

.upload-section {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  margin-bottom: 20px;
}

.upload-section h3 {
  color: #61dafb;
  margin: 0 0 20px 0;
  font-size: 1.2rem;
}

.upload-group {
  margin-bottom: 20px;
}

.upload-group label {
  display: block;
  margin-bottom: 8px;
  color: #ffffffc6;
  font-weight: 500;
}

.upload-group input[type="file"] {
  width: 100%;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: #ffffffc6;
  cursor: pointer;
}

.upload-group input[type="file"]::-webkit-file-upload-button {
  background-color: rgba(97, 218, 251, 0.15);
  color: #61dafb;
  padding: 8px 16px;
  border: 1px solid rgba(97, 218, 251, 0.4);
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  transition: all 0.2s;
}

.upload-group input[type="file"]::-webkit-file-upload-button:hover {
  background-color: rgba(97, 218, 251, 0.3);
  border-color: #61dafb;
}

.file-name {
  display: block;
  margin-top: 5px;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.cancel-button {
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffffc6;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.2s;
}

.cancel-button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.submit-button {
  background-color: rgba(39, 174, 96, 0.15);
  color: #27ae60;
  border: 1px solid rgba(39, 174, 96, 0.4);
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.submit-button:hover {
  background-color: rgba(39, 174, 96, 0.3);
  border-color: #27ae60;
}

/* Responsive styles */
@media (max-width: 768px) {
  .internships-filters {
    flex-direction: column;
  }
  
  .search-group input {
    width: 100%;
  }
  
  .filter-stats {
    margin-left: 0;
    justify-content: flex-start;
  }
  
  .internships-list {
    grid-template-columns: 1fr;
  }
  
  .internship-card-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .internship-status {
    margin-top: 10px;
  }
  
  .company-detail {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

.form-section {
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  margin-bottom: 20px;
}

.form-section h3 {
  color: #61dafb;
  margin: 0 0 20px 0;
  font-size: 1.2rem;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #ffffffc6;
  font-weight: 500;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group textarea {
  width: 100%;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: #ffffffc6;
  font-size: 0.9rem;
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

.form-group input[type="text"]:focus,
.form-group input[type="email"]:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #61dafb;
  box-shadow: 0 0 0 2px rgba(97, 218, 251, 0.2);
}

.search-bar {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.search-bar input {
  flex: 1;
  min-width: 200px;
  padding: 10px 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.05);
  color: #ffffffc6;
  font-size: 0.9rem;
}

.search-bar input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.filter-toggle {
  padding: 10px 20px;
  background-color: rgba(97, 218, 251, 0.15);
  color: #61dafb;
  border: 1px solid rgba(97, 218, 251, 0.4);
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.filter-toggle:hover {
  background-color: rgba(97, 218, 251, 0.3);
  border-color: #61dafb;
}

.filters-section {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  flex-wrap: wrap;
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: 5px;
  min-width: 200px;
}

.filter-group label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

.filter-group select {
  padding: 8px 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.05);
  color: #ffffffc6;
  font-size: 0.9rem;
  cursor: pointer;
}

.filter-group select:hover {
  border-color: rgba(255, 255, 255, 0.2);
}

.filter-group select:focus {
  outline: none;
  border-color: #61dafb;
  box-shadow: 0 0 0 2px rgba(97, 218, 251, 0.2);
}

.filter-group select option {
  background-color: #2c2c2c;
  color: #ffffffc6;
}

.filter-stats {
  margin: 15px 0;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

/* Responsive styles */
@media (max-width: 768px) {
  .search-bar {
    flex-direction: column;
  }

  .search-bar input,
  .filter-toggle {
    width: 100%;
  }

  .filters-section {
    flex-direction: column;
  }

  .filter-group {
    width: 100%;
  }
}