.breadcrumbs {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 8px 16px;
  margin: 0 0 15px 0;
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 4px;
}

.breadcrumb-item {
  font-size: 14px;
  color: #666;
  padding: 2px 4px;
}

.breadcrumb-item.active {
  color: #1890ff;
  font-weight: 500;
}

.breadcrumb-item.clickable {
  cursor: pointer;
  color: #1890ff;
  transition: color 0.2s;
}

.breadcrumb-item.clickable:hover {
  color: #40a9ff;
  text-decoration: underline;
}

.breadcrumb-separator {
  color: #ccc;
  margin: 0 5px;
  user-select: none;
}

/* Dark theme */
.dark-theme .breadcrumbs {
  background-color: rgba(255, 255, 255, 0.05);
}

.dark-theme .breadcrumb-item {
  color: #bbb;
}

.dark-theme .breadcrumb-item.active {
  color: #40a9ff;
}

.dark-theme .breadcrumb-item.clickable {
  color: #40a9ff;
}

.dark-theme .breadcrumb-item.clickable:hover {
  color: #69c0ff;
}

.dark-theme .breadcrumb-separator {
  color: #666;
} 